.OrderButton {
    margin-top: 22px;
    background-color:#F9E02A;
    outline: none;
    cursor: pointer;
    border: none;
font-weight: bold;
    font-family: inherit;
    font-size: 1.2em;
padding: 20px 0px;
   
    
    width: 300px;
    border-radius: 7.5px;
}

.OrderButton:hover, .OrderButton:active {
    position: relative; 
    top: -1px;
    box-shadow: 2px 5px 2px #eee;
}

.OrderButton:disabled {
    background-color: #dbdfe2;
    cursor: not-allowed;

    color: #B3B6B8;
}

.OrderButton:not(:disabled) {
    -webkit-animation: enable 0.3s linear;
            animation: enable 0.3s linear;
}

@-webkit-keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
